<script>
export default {
  name: 'PaymentPage',
  data() {
    return {
      patientName: '李*',
      date: '2025.3.19',
      department: '神经内科',
      doctorName: '张一统',
      fee: 470,
      selectedMethod: 'wechat' // 默认选择微信支付
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    selectMethod(method) {
      this.selectedMethod = method;
    },
    goToPaymentSuccess() {
      // 设置支付状态为已完成
      localStorage.setItem('paymentStatus', 'completed');
      
      // 跳转到支付成功页面并传递数据
      this.$router.push({
        path: '/payment-success',
        query: {
          fee: this.fee,
          patientName: this.patientName,
          date: this.date,
          department: this.department,
          doctorName: this.doctorName,
          fromPaymentPage: 'true', // 添加标记，表示来自支付页面
          paymentMethod: this.selectedMethod // 添加支付方式
        }
      });
    }
  }
}
</script>

