        <div class="item-fee">350元</div>
      </div>
    </div>

    <!-- 距离信息 -->
    <div class="distance-info">
      <div :class="fromPaymentSuccess ? 'distance-text-paid' : 'distance-text'">当前距离: 672m</div>
