            v-if="completedExam === '心电图'"
            class="queue-button completed-button"
          >已完成</div>
          <div 
            v-else
            :class="fromPaymentSuccess ? 'queue-button active-button' : 'queue-button disabled-button'" 
            @click="fromPaymentSuccess && startQueue('心电图')"
          >开始排队</div>
        </div>
        
        <div class="item-details-row">
          <div class="detail-status">
            <img src="../assets/heart-icon1.png" alt="心" class="heart-icon" />
            当前检查：暂无
          </div>
        </div>
        
        <div class="queue-info">
          <div :class="fromPaymentSuccess ? 'waiting-count-paid' : 'waiting-count'">
