            等待人数: 6位
          </div>
          <div :class="fromPaymentSuccess ? 'waiting-time-paid' : 'waiting-time'">
            <img src="../assets/clock-icon.png" alt="时钟" class="info-icon" />
            预计等待: 26分
          </div>
        </div>
      </div>
