    <!-- 查看进度按钮 -->
    <div 
      :class="fromPaymentSuccess ? 'check-progress-button-paid active-button' : 'check-progress-button disabled-button'"
      @click="fromPaymentSuccess && checkQueueProgress()"
    >
      查看当前叫号进度
    </div>

   <!-- 底部导航栏 -->
<div class="bottom-nav">
  <div class="nav-item active" @click="goToHomePage">
    <div class="nav-icon">
      <img src="../assets/home-icon1.png" alt="首页" class="nav-icon-img" />
    </div>
    <div class="nav-text">首页</div>
  </div>
