  <img src="../assets/mic-icon.png" alt="语音" class="voice-icon-img" />
</div>
</div>
</template>
<script>
export default {
  name: 'MedicalReminder',
  data() {
    return {
      isPaid: false,
      fromPaymentSuccess: false,
      completedExam: '' // 新增已完成的检查类型
    }
  },
  created() {
    // 检查是否从支付成功页面跳转过来
    this.fromPaymentSuccess = this.$route.query.fromPaymentSuccess === 'true';
    
    // 获取已完成的检查类型
    this.completedExam = this.$route.query.completedExam || localStorage.getItem('completedExam') || '';
    
    // 如果是从PaymentSuccess页面跳转过来，清除之前可能存在的completedExam
    if (this.$route.query.fromPaymentSuccess === 'true' && !this.$route.query.completedExam) {
      localStorage.removeItem('completedExam');
      this.completedExam = '';
    }
    
    // 检查是否从导航页面跳转过来
    if (this.$route.query.fromNavigation === 'true') {
      // 如果是从导航页面跳转过来，重置支付状态
      localStorage.removeItem('paymentStatus');
      this.isPaid = false;
    } else {
      // 否则检查支付状态
      this.checkPaymentStatus();
    }
  },
  methods: {
    // 获取进度条图片
    getProgressImage() {
      // 检查是否有已完成的检查
      if (this.completedExam) {
        // 如果有一个检查已经完成，使用 load13.png
        return 'load13.png';
      } else {
        // 如果两个检查都没开始，根据是否从支付成功页面跳转来决定
        return this.fromPaymentSuccess ? 'load8.png' : 'load6.png';
      }
    },
    
    // 跳转到首页
    goToHomePage() {
      // 确保从就诊提醒页面点击首页按钮时，不会显示已完成所有检查的页面
      // 临时移除 allExamsCompleted 标记
      localStorage.removeItem('allExamsCompleted');
      this.$router.push('/home');
    },
    
    // 其他方法保持不变...
    // 跳转到方言转换页面
    goToDialectTranslation() {
      this.$router.push('/dialect-translation');
    },
    // 跳转到缴费页面
    goToPayment() {
      if (!this.isPaid) {
        this.$router.push('/payment');
      }
    },
    // 检查支付状态
    checkPaymentStatus() {
      // 这里可以从本地存储或其他地方获取支付状态
      const paymentStatus = localStorage.getItem('paymentStatus');
      this.isPaid = paymentStatus === 'completed';
    },
    // 跳转到导航页面
    goToNavigationpage() {
      // 只有在已缴费的情况下才能导航
      if (this.isPaid) {
        this.$router.push({
          path: '/navigation-page',
          query: { 
            fromMedicalReminder: 'true'
          }
        });
      }
    },
    // 开始排队
    startQueue(examType) {
      // 存储检查类型
      localStorage.setItem('currentExamType', examType);
      
      // 检查是否有已完成的检查
      const completedExam = localStorage.getItem('completedExam');
      
      // 跳转到排队页面
      this.$router.push({
        path: '/queue-progress',
        query: { 
          examType: examType,
          status: 'waiting',
          completedExam: completedExam || ''
        }
      });
    },
    // 查看排队进度
    // 修改 checkQueueProgress 方法
    checkQueueProgress() {
    // 检查是否有已完成的检查
    const completedExam = this.completedExam;
    
    // 如果有一个检查已经完成，直接进入未完成的检查页面
    if (completedExam === '内科检查') {
    // 内科检查已完成，进入心电图排队页面
    this.startQueue('心电图');
    } else if (completedExam === '心电图') {
    // 心电图已完成，进入内科检查排队页面
    this.startQueue('内科检查');
    } else {
    // 两个检查都没有开始排队，选择等待人数少的那个
    // 内科检查等待人数为3，心电图等待人数为6，选择内科检查
    this.startQueue('内科检查');
    }
    }
  }
}
</script>
 


