<template>
  <div class="navigation-container">
    <!-- 顶部进度条 -->

    <!-- 导航指示 -->
    <div class="navigation-content">
      <div class="navigation-indicator">
        <div class="direction-info">
          <div class="direction-icon">
            <img src="../assets/turn-right.png" alt="右转" />
          </div>
          <div class="direction-text">直行<span class="distance">15</span>米<br/>右转</div>
        </div>
        <div class="floor-info">
          <div class="floor-label">当前楼层</div>
          <div class="floor-number">1楼</div>
        </div>
      </div>
    </div>

    <!-- 导航地图 -->
    <div class="hospital-navigation">
      <img src="../assets/hospital-map.png" alt="医院地图" class="map-image" />
      <!-- 导航路径 -->
      <div class="navigation-path"></div>
    </div>

    <!-- 底部导航信息 -->
    <div class="navigation-footer">
      <div class="navigation-info">
        <div class="info-row">
          <div class="info-label">全程剩余：</div>
          <div class="info-value">278米</div>
        </div>
        <div class="info-row">
          <div class="info-label">预计用时：</div>
          <div class="info-value">2 分钟</div>
        </div>
      </div>
      <div class="complete-status" @click="goBack()">
        <div class="check-icon">
          <img src="../assets/confirm.png" alt="确认" />
        </div>
        <div class="status-text">完成</div>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: 'NavigationPage',
  data() {
    return {
      // 导航相关数据
    }
  },
  methods: {
    // 返回医疗提醒页面或首页
    goBack() {
      // 获取当前已完成的检查类型
      const completedExam = localStorage.getItem('completedExam') || '';
      
      // 设置支付状态为已完成，确保返回后缴费信息显示为已完成
      localStorage.setItem('paymentStatus', 'completed');
      
      // 判断是从哪个页面来的
      const fromMedicalReminder = this.$route.query.fromMedicalReminder === 'true';
      const fromReport = this.$route.query.fromReport === 'true';
      
      if (fromMedicalReminder) {
        // 如果是从医疗提醒页面来的，返回医疗提醒页面
        this.$router.push({
          path: '/medical-reminder',
          query: { 
            fromNavigation: 'false',
            fromPaymentSuccess: 'true', // 保持支付成功状态
            completedExam: completedExam // 传递已完成的检查类型
          }
        });
      } else if (fromReport) {
        // 如果是从报告查询页面来的，返回首页（去挂号的首页）
        this.$router.push({
          path: '/home',
          query: { 
            showReportPage: 'false'
          }
        });
      } else {
        // 默认返回首页（去挂号的首页）
        this.$router.push({
          path: '/home',
          query: { 
            showReportPage: 'false'
          }
        });
      }
    }
  }
}
</script>
  
